import React, { useEffect } from 'react';

import { useNavigate, useParams } from 'react-router-dom';

import Styles from './InternalServer.module.css';
import cloudIcon from '../../../assets/icons/Cloud.png';
import op2miseLogo from '../../../assets/icons/op2mise.svg';

import FooterComponent from '../../../shared/footer/footer.component';

function InternalServerComponent() {
  const navigate = useNavigate();
  const { message } = useParams();

  useEffect(() => {
    // eslint-disable-next-line
    console.log(`error 500 message: ${message}`);
    // eslint-disable-next-line
  }, []);

  const navigateToHome = () => {
    navigate(`${process.env.REACT_APP_SUBFOLDER}/`);
  };

  return (
    <div
      style={{
        height: '100vh',
        width: '100vw',
        position: 'relative',
      }}
    >
      <div className={`${Styles.component} text-white text-center`}>
        <img className={Styles.logo} src={op2miseLogo} alt="op2miseLogo" />
        <p className={Styles.description1}>Sorry, something went wrong</p>
        <p className={Styles.description2}>
          It&apos;s not you, it&apos;s us. Please try again later.
          <br />
          Please contact us at
          {' '}
          <span style={{ color: '#2d6b9a' }}>support@op2mise.tv</span>
          {' '}
          if
          this problem persists.
        </p>
        <img className={Styles.cloudIcon} src={cloudIcon} alt="Cloud-Icon" />
        <br />
        <button
          className={Styles.navigateButton}
          onClick={navigateToHome}
          type="button"
        >
          Home
        </button>
      </div>
      <FooterComponent />
    </div>
  );
}

export default InternalServerComponent;
